<template>
    <v-container id="template" >
        <v-btn id="print" class="blue white--text" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="ml-4 red white--text" to="/bukutahunan" link>Back</v-btn>
        <v-btn v-if="statusButton" id="print" class="ml-4 green accent-4 white--text" @click="updateSaldo()">Update Saldo</v-btn>

       <template id="template">
           <br><h2 class="text-center">Buku Besar</h2>
            <v-row>
                <v-col cols="6">
                    <h4 class="text-left">No Perkiraan: {{kodes.KodeUnik}}</h4>
                </v-col>
                <v-col cols="6">
                    <h4 class="text-right">Nama Perkiraan: {{kodes.JenisPerkiraan}}</h4>
                </v-col>
            </v-row>
           <table id="table" border="1" cellspacing="0" cellpadding="0">
                <thead>
                    <tr style="font-size: 9pt">
                        <th id="th">Tanggal</th>
                        <th id="th">No Bukti</th>
                        <th id="th">Keterangan</th>
                        <th id="th">Kode Lawan</th>
                        <th id="th">Debet</th>
                        <th id="th">Kredit</th>
                        <th id="th">Saldo</th>
                    </tr>
                </thead>
                <tbody>
                    <tr style="font-size: 9pt">
                        <td class="text-center" id="td">{{kodes["Tanggal"]}}</td>
                        <td id="td"></td>
                        <td id="td">Saldo Awal</td>
                        <td id="td"></td>
                        <td id="td"></td>
                        <td id="td"></td>
                        <td class="text-right" id="td">{{kodes["Debet"] | currency}}</td>
                    </tr>

                    <tr v-for="item in bukus" :key='item.id' style="font-size: 9pt">
                        <td id="td" width="75.5px" class="text-center" nowrap="">{{item["tanggal"]}}</td>
                        <td id="td" width="56.6px" class="text-center" nowrap="" v-if="item.nobukti == 'null'"></td>
                        <td id="td" width="56.6px" class="text-center" nowrap="" v-else>{{item["nobukti"]}}</td>
                        <td id="td" width="190px" nowrap="">{{item["keterangan"]}}</td>

                        <td class="text-center" id="td" width="75px" nowrap=""> {{ item["kode"]}} </td>
                        <td id="td" width="95px" class="text-right" nowrap="">{{item["debet"] | currency}} </td>
                        <td id="td" width="95px" class="text-right" nowrap="">{{item["kredit"] | currency }} </td>
                        <td class="text-right" id="td" width="110px">Rp {{item.total | currency}}</td>
                    </tr>

                    <tr>

                        <!-- <td  id="td" colspan="2" class="text-right"><pre><b>{{this.totalpembelians}}</b></pre></td> -->
                    </tr>
                </tbody>
            </table>
       </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                keyword: '',
                load: false,
                bukus: [],
                kodes: {},
                total: 0,
                saldoTable: 0,
                totalDebet: 0,
                totalKredit: 0,
                statusButton: false,
                updatedId: '',
                form: {
                    jumlah: '',
                    debet: '',
                    kredit: '',
                    kodes: '',
                },
                listperkiraan: new FormData,

            }
        },
        methods: {
            getData() {
                console.log(this.$route.params.radios)
                if (this.$route.params.radios == "Buku Besar") {
                    var uri = this.$apiUrl + 'bukutahunan/buku_besar_report/' + this.$route.params.kode
                    this.$http.get(uri).then(response => {
                        this.bukus = response.data
                        var uri2 = this.$apiUrl + 'listperkiraan/' + this.$route.params.kode
                        this.$http.get(uri2).then(response => {
                            this.kodes = response.data[0]
                            this.saldoTable = this.kodes.Debet
                            for (var x in this.bukus) {
                                this.bukus[x].total = this.saldoTable
                                // ini dibalik semua, normalnya this.totalSeluruh -= this.bukus[x].jumlah untuk kredit, untuk debet di plus
                                this.totalKredit += this.bukus[x].kredit;
                                this.totalDebet += this.bukus[x].debet;
                                this.saldoTable += this.bukus[x].debet
                                this.saldoTable -= this.bukus[x].kredit
                                this.bukus[x].total = this.saldoTable
                            }
                        })

                    })
                } else if (this.$route.params.radios == "Buku Besar Daerah") {
                    var uri = this.$apiUrl + 'bukutahunan/buku_besar_daerah_report/' + this.$route.params.kode
                    this.$http.get(uri).then(response => {
                        this.bukus = response.data
                        var uri2 = this.$apiUrl + 'listperkiraan/' + this.$route.params.kode
                        this.$http.get(uri2).then(response => {
                            this.kodes = response.data[0]
                            this.saldoTable = this.kodes.Debet
                            for (var x in this.bukus) {
                                this.bukus[x].total = this.saldoTable
                                // ini dibalik semua, normalnya this.totalSeluruh -= this.bukus[x].jumlah untuk kredit, untuk debet di plus
                                this.totalKredit += this.bukus[x].kredit;
                                this.totalDebet += this.bukus[x].debet;
                                this.saldoTable += this.bukus[x].debet
                                this.saldoTable -= this.bukus[x].kredit
                                this.bukus[x].total = this.saldoTable
                            }
                        })

                    })

                }
            },

            updateSaldo() {
               
                // this.form.debet = this.totalDebet;
                // this.form.kredit = this.totalKredit;
                // this.form.kodes = this.totalSeluruh;
                this.updatedId = this.$route.params.kode
                // console.log(this.updatedId);
                this.listperkiraan.append('jenisperkiraan', this.kodes.JenisPerkiraan);
                this.listperkiraan.append('kodeunik', this.kodes.KodeUnik);
                this.listperkiraan.append('kumulatifdebet', this.totalDebet);
                this.listperkiraan.append('kumulatifkredit', this.totalKredit);
                if(this.totalSeluruh < 0){
                    this.listperkiraan.append('kredit', this.saldoTable);
                }else{
                    this.listperkiraan.append('debet',this.saldoTable);
                }
                var uri = this.$apiUrl + 'listperkiraan/' + this.updatedId;
                this.load = true
                this.$http.post(uri, this.listperkiraan).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackba
                    this.load = false;
                    this.dialog = false
                    this.statusUpdate = true
                    this.statusButton = false
                    this.typeInput = 'new';
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                    this.typeInput = 'new';
                })

            },

            printReport() {
                window.print()
                this.statusButton=true
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style>
    @media print{
        #print{
            display: none;
        }
        #template{
            background-color: white
        }
    }
    
    #table, #th, #td {
        border: 1.5px solid black;
        border-collapse: collapse;
    }
</style>